body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.logo {
  width: 100px;
  height: 54px;
  padding-top: 7px;
  padding-bottom: 3px;
  float: left;
}

.back-top {
  position: fixed;
  bottom: 80px;
  right: 40px;
  font-size: 30px;
  cursor: pointer;
}

/* 语法高亮样式 */
.lang-javascript {
  border-radius: 6px;
}

/* 公共样式 */
.common-wrap {
  display: -webkit-flex;
  display: flex;
  padding: 20px;
}

.fr {
  float: right;
}

.fl {
  float: left;
}

.home-wrap {
    
}

.home-wrap .content {
    -webkit-flex: 1 1;
            flex: 1 1;
}

.js-tool-fn-wrap {
    margin-bottom: 30px;
}

.js-tool-fn-wrap .desc {
    padding-bottom: 10px;
}

.home-wrap .side-bar {
    width: 280px;
    margin-left: 20px;
}

.online-tool {
    width: 80%;
    margin: 20px auto;
}

.online-tool .search-wrap {
    width: 60%;
    margin: 30px auto;
}

.online-tool .content {
    margin-top: 20px;
}

.online-tool .content .item-wrap {
    width: 33.3%;
    display: inline-block;
    padding-right: 16px;
    margin-bottom: 20px;
    cursor: pointer;
}

.online-tool .content .item-wrap:hover .ant-card {
    box-shadow: 0 0 20px rgba(0,0,0,.1);
}

.online-tool .content .item-wrap .ant-card .ant-card-body { 
    position: relative;
    height: 100px;  
    overflow:hidden; 
    text-overflow:ellipsis;
    display:-webkit-box;
    -webkit-line-clamp:2;
}

.online-tool .content .item-wrap .ant-card .ant-card-body::after {
    position: absolute;
    bottom: -1px;
    left: 0;
    content: '';
    display:block;
    width: 100%;
    height: 1px;
    box-shadow: 0 0 26px rgba(0, 0, 0, .6);
}

.online-tool .ant-empty {
    margin-top: 120px;
}
.frame-wrap {
    width: 75%;
    margin: 40px auto;
    
}

.frame-wrap .ant-table-wrapper {
    background-color: #fff;
}
